import React,{useEffect, useState, useContext} from "react";
import {Link } from "react-router-dom";
import MetaTags from 'react-meta-tags';

import toast, { Toaster } from 'react-hot-toast';
import {BaseUrlContext} from '../App';

const Contact = ()=>{

    useEffect(() => {
        //window.scrollTo({top: 0, behavior: 'smooth'});

        const loader = document.getElementById('preloader');

        if(Isloading){
            loader.style.display = "block";
        }else{
            loader.style.display = "none";
        }
    });

    const base_url = useContext(BaseUrlContext);
    const [Isloading, setIsLoaded] = useState(false);
    

    const handleSubmit = async e => {
        e.preventDefault();
        const data =new FormData(e.target);
        const name = data.get('name');
        const email = data.get('email');
        const mobile = data.get('mobile');
        const subject = data.get('subject');
        const message = data.get('message');
        setIsLoaded(true);
        
        fetch(`${base_url}/contact-submit`,{
        method: 'POST',
        headers: {
            Accept: 'application/json',
                    'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            name,email,mobile,subject,message
          })
        }).then(res => res.json())
        .then(
            (result) => {
                setIsLoaded(false);
                console.log(result.data);
            if(result.error){
                toast.error(result.message, {position: 'bottom-center',style: {background: '#000',color:'#fff'},iconTheme: {primary: 'green',secondary: '#fff',},})
                            
            }else{
                toast.success(result.message, {position: 'bottom-center',style: {background: '#000',color:'#fff'},iconTheme: {primary: 'green',secondary: '#fff',},})
                
            }
            
            },
            (error) => {
                setIsLoaded(false);
                toast.error(error, {position: 'bottom-center',style: {background: '#000',color:'#fff'},iconTheme: {primary: 'green',secondary: '#fff',},})

            }
        )
        
    }

    return(
        <>
            <MetaTags>
                <title>Contact Page</title>
                <meta name="description" content="Contact page" />
            </MetaTags>
            <main className="main pages">
                
                <div className="page-content pt-50">
                    
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-10 col-lg-12 m-auto">
                                <section className="mb-50">
                                    <div className="row mb-60">
                                        <div className="col-md-4 mb-4 mb-md-0">
                                            <h4 className="mb-15 text-brand">Office</h4>
                                            205 North Michigan Avenue, Suite 810<br /> Chicago, 60601, USA<br />
                                            <abbr title="Phone">Phone:</abbr> (123) 456-7890<br />
                                            <abbr title="Email">Email: </abbr><a href="/cdn-cgi/l/email-protection" className="__cf_email__" data-cfemail="4c2f2322382d2f380c093a2d3e2d622f2321">[email&#160;protected]</a><br />
                                            <a className="btn btn-sm font-weight-bold text-white mt-20 border-radius-5 btn-shadow-brand hover-up"><i className="fi-rs-marker mr-5"></i>View map</a>
                                        </div>
                                        <div className="col-md-4 mb-4 mb-md-0">
                                            <h4 className="mb-15 text-brand">Studio</h4>
                                            205 North Michigan Avenue, Suite 810<br /> Chicago, 60601, USA<br />
                                            <abbr title="Phone">Phone:</abbr> (123) 456-7890<br />
                                            <abbr title="Email">Email: </abbr><a href="/cdn-cgi/l/email-protection" className="__cf_email__" data-cfemail="a9cac6c7ddc8cadde9ecdfc8dbc887cac6c4">[email&#160;protected]</a><br />
                                            <a className="btn btn-sm font-weight-bold text-white mt-20 border-radius-5 btn-shadow-brand hover-up"><i className="fi-rs-marker mr-5"></i>View map</a>
                                        </div>
                                        <div className="col-md-4">
                                            <h4 className="mb-15 text-brand">Shop</h4>
                                            205 North Michigan Avenue, Suite 810<br /> Chicago, 60601, USA<br />
                                            <abbr title="Phone">Phone:</abbr> (123) 456-7890<br />
                                            <abbr title="Email">Email: </abbr><a href="/cdn-cgi/l/email-protection" className="__cf_email__" data-cfemail="3a5955544e5b594e7a7f4c5b485b14595557">[email&#160;protected]</a><br />
                                            <a className="btn btn-sm font-weight-bold text-white mt-20 border-radius-5 btn-shadow-brand hover-up"><i className="fi-rs-marker mr-5"></i>View map</a>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-xl-8">
                                            <div className="contact-from-area padding-20-row-col">
                                                <h5 className="text-brand mb-10">Contact form</h5>
                                                <h2 className="mb-10">Drop Us a Line</h2>
                                                <p className="text-muted mb-30 font-sm">Your email address will not be published. Required fields are marked *</p>
                                                <form className="contact-form-style mt-30" id="contact-form" onSubmit={handleSubmit} method="post">
                                                    <div className="row">
                                                        <div className="col-lg-6 col-md-6">
                                                            <div className="input-style mb-20">
                                                                <input name="name" placeholder="First Name" required type="text" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6 col-md-6">
                                                            <div className="input-style mb-20">
                                                                <input name="email" placeholder="Your Email" required type="email" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6 col-md-6">
                                                            <div className="input-style mb-20">
                                                                <input name="mobile" placeholder="Your Phone" required type="tel" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6 col-md-6">
                                                            <div className="input-style mb-20">
                                                                <input name="subject" placeholder="Subject" required type="text" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-12 col-md-12">
                                                            <div className="textarea-style mb-30">
                                                                <textarea name="message" placeholder="Message" required></textarea>
                                                            </div>
                                                            <button className="submit submit-auto-width" type="submit" style={{display:"flex"}}>Send message &nbsp; 
                                                                <div class="spinner-border " id="preloader" role="status" style={{display:"none"}}>
                                                                    <span class="sr-only">Loading...</span>
                                                                </div>
                                                            </button>
                                                        </div>
                                                    </div>
                                                </form>
                                                <p className="form-messege"></p>
                                            </div>
                                        </div>
                                        <div className="col-lg-4 pl-50 d-lg-block d-none">
                                            <img className="border-radius-15 mt-50" src="assets/imgs/page/contact-2.png" alt="" />
                                        </div>
                                    </div>
                                </section>
                            </div>
                        </div>
                    </div>
                    
                    <section className="container mb-50 d-none d-md-block">
                        <div className="border-radius-15 overflow-hidden">
                            <div id="map-panes" className="leaflet-map"></div>
                        </div>
                    </section>
                </div>
            </main>
            
        </>
    )
}
export default Contact;